
import axios from 'axios'
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'pokeView',
  data() {
    return {
      requestedPokemon: [] as any[any],
      statsColors:{
        hp:"bg-red-500",
        attack:"bg-yellow-600",
        defense:"bg-blue-500",
        'special-attack':"bg-orange-600",
        'special-defense':"bg-sky-300",
        speed:"bg-green-600"
      } as any[any],
      statsLabels:{
        attack: 'ATK',
        hp:"HP",
        defense:"DEF",
        'special-attack':"SA",
        'special-defense':"SD",
        speed:"SPD"
      } as any[any],
      colors:{
            grass: 'bg-green-400',
            fire: 'bg-orange-500',
            water:'bg-sky-400',
            bug:'bg-lime-500',
            electric:'bg-yellow-500',
            normal:'bg-gray-400',
            poison:'bg-violet-400',
            ground:'bg-amber-600',
            fairy:'bg-rose-300',
            fighting:'bg-orange-700',
            psychic:'bg-pink-600',
            rock:'bg-yellow-700',
            ghost:'bg-purple-600',
            ice:'bg-cyan-300',
            dragon:'bg-red-500',
            flying:'bg-sky-300'
          } as any[any]

    }
  },
  async mounted() {
    //recupero el id de la url (de la variable que definí en el router)
    const id = this.$route.params.pokemonid

    //hago la llamada a la api para buscar el pokemon con ese id
    const response = await axios.get('https://pokeapi.co/api/v2/pokemon/' + id)

    //ASIGNO LA RESPUESTA A LA VARIABLE DINAMICA PARA USAR EN MAQUETA
    this.requestedPokemon = response.data

    console.log(response.data)

  },
})
